$yellow: yellow;
$white: #fff;
$black: #000;
$red: #ef0716;
$bg: #f1f2f4;
@mixin active {
    color: $white;
    background: $red;
}

@mixin btn_red {
    color: $white;
    background: $red;
}

@mixin color($r, $g, $b, $a) {
    color: rgba($r, $g, $b, $a);
}

@mixin pointer {
    cursor: pointer;
}

$width: 368px;
$anchor_color: rgba(246, 85, 156, 1);
$consume_color: rgba(144, 71, 186, 1);
.ranking-page {
  width: 100%;
  height: 100%;
  .container {
    width: 1400px;
    height: 100%;
    margin: 30px auto 0;
    .ranking_list {
      display: inline-block;
    }
    .ranking_rank {
      .borderTop {
        border-top: 1px solid rgba(221, 221, 221, 1);
      }
      .ant-tabs {
        background: $white;
        .ant-tabs-bar {
          margin-bottom: 0;
        }
      }
      .ant-tabs-nav {
        display: -webkit-flex;
        display: flex;
        > div {
          width: 100%;
        }
        .ant-tabs-tab {
          width: $width / 3;
          height: 50px;
          margin: 0;
          font-size: 16px;
          line-height: 26px;
          text-align: center;
        }
      }
    }
    .anchor {
      .ant-tabs-nav {
        .ant-tabs-tab {
          &:hover {
            color: $anchor_color;
          }
        }
        .ant-tabs-tab-active {
          color: $anchor_color;
        }
      }
      .ant-tabs-ink-bar {
        background-color: $anchor_color;
      }
      .title span {
        background: $anchor_color;
      }
    }
    .consume {
      .ant-tabs-nav {
        .ant-tabs-tab {
          &:hover {
            color: $consume_color;
          }
        }
        .ant-tabs-tab-active {
          color: $consume_color;
        }
      }
      .ant-tabs-ink-bar {
        background-color: $consume_color;
      }
      .title span {
        background: $consume_color;
      }
    }
  }

}
